import axios from "axios";
import React, { Component, Fragment, useEffect, useState } from "react";
import { IoMdBookmark, IoMdInformationCircleOutline } from "react-icons/io";
import { MdCheckCircle } from "react-icons/md";
import { RiErrorWarningLine } from "react-icons/ri";
import { Redirect } from "react-router";
import {
  applicationPeriodsStoreInterface,
  Auth,
  CandidateApplicationsStoreInterface,
  candidateApplyStoreInterface,
  Interface_CategorySubCategoryData,
} from "../../../../actions";
import { AddUserDocument } from "../../../../components/AddUserDocument/AddUserDocument";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import Loading from "../../../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../../../components/Modal/Modal";
import {
  DocumentsCategories,
  DocumentSubCategories,
  PositionAPI,
  PositionRequirements,
  PositionSubcategoryData,
  returnValidatePosition,
  returnValidatePositionOptional,
  validateDocCategory,
  validateDocSubcategory,
  validateDocSubcategoryOptional,
} from "../../../../interfaces";
import { GetApplicationResponse } from "../../../../interfaces/application";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { API } from "../../../../utils/api";
import {
  sentExamInterface,
  SubmitApplicationAPI,
} from "../../../Application/ExamCenter/ExamCenter";
import { ApplicationStepsTypes } from "../CandidateApplicationSteps/CandidateApplicationSteps";
import EmptyFile from "./EmptyFile";
import SuccessFile from "./SuccessFile";

interface CandidateApplyRequirementsProps {
  auth: Auth;
  currentPosition: PositionAPI | null;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  FC_GetProfileDetails: Function;
  initializeApplicationProcess: (
    position: PositionAPI | null,
    user_id: string | null
  ) => void;
  candidateApply: candidateApplyStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  setCandidateApplicationStep: (step: ApplicationStepsTypes) => void;
  sendApplication: () => void;
  candidateApplications: CandidateApplicationsStoreInterface;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
}
interface CandidateApplyRequirementsState {
  selectedRequirement: {
    selected_category: DocumentsCategories | null;
    selected_sub_category: DocumentSubCategories | null;
    selected_category_subcategory_data?: Interface_CategorySubCategoryData | null;
    status: boolean;
  } | null;
  submitWithoutRequirements: boolean;
  redirect: boolean;
  loading: boolean;
  loadUserDetails: boolean;
  requirementDescriptions: boolean;
}

class CandidateApplyRequirements extends Component<
  CandidateApplyRequirementsProps,
  CandidateApplyRequirementsState
> {
  constructor(props: CandidateApplyRequirementsProps) {
    super(props);

    this.state = {
      selectedRequirement: null,
      submitWithoutRequirements: false,
      redirect: false,
      loading: false,
      loadUserDetails: true,
      requirementDescriptions: false,
    };
  }

  sendApplication = async () => {
    // Validation
    if (
      this.props.auth.isAuthenticated === true &&
      this.props.auth.user.user_id !== ""
    ) {
      if (this.props.candidateApply.selectedPosition !== null) {
        let currentYear: Date = new Date();
        let exams: sentExamInterface[] = [];
        const applicationDetails: SubmitApplicationAPI = {
          user_id: this.props.auth.user.user_id,
          fname: this.props.auth.user.fname,
          lname: this.props.auth.user.lname,
          email: this.props.auth.user.email,
          phone_number: this.props.auth.user.phone_number,
          position: this.props.candidateApply.selectedPosition.position_id,
          application_period: `${currentYear.getFullYear()}-${
            currentYear.getFullYear() + 1
          }`,
          exams: exams,
        };
        try {
          this.setLoading(true);
          const res = await axios.post<GetApplicationResponse>(
            `${API.APPLICATION}/application`,
            applicationDetails
          );
          // Load my applications
          this.props.LoadCandidateApplications(
            this.props.auth.user.user_id,
            this.setLoading
          );
          this.setState({ redirect: true });
          // console.log("Application res: ", res);
          if (res.status === 200) {
            this.props.PushPopupAlert({
              title: `Application created successfully`,
              theme: PopupAlertTheme.success,
            });
          }
          this.setLoading(false);
        } catch (error: any) {
          this.setLoading(false);
          if (error.response) {
            if (error.response.data) {
              if (error.response.data.errors !== undefined) {
                if (error.response.data.errors.length > 0) {
                  this.props.PushPopupAlert({
                    title: error.response.data.errors[0].message,
                    theme: PopupAlertTheme.warning,
                  });
                }
              } else {
                this.props.PushPopupAlert({
                  title: "Something went wrong! Please try again",
                  theme: PopupAlertTheme.danger,
                });
              }
            }
          }
          console.log("Submit Application err: ", { ...error });
        }
      }
    }
  };

  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  validateRequirementItem = (
    item: PositionRequirements,
    itm: PositionSubcategoryData
  ) => {
    let x = validateDocSubcategory(item, this.props.auth.documents).find(
      (foundData) => itm._id === foundData.category_subcat_id
    );
    return x !== null && x !== undefined ? false : true;
  };

  validateRequirementItemOptional = (
    item: PositionRequirements,
    itm: PositionSubcategoryData
  ) => {
    let x = validateDocSubcategoryOptional(
      item,
      this.props.auth.documents
    ).find((foundData) => itm._id === foundData.category_subcat_id);
    return x !== null && x !== undefined ? false : true;
  };

  FilteredList = (): PositionRequirements[] | null => {
    let newData: PositionRequirements[] = [];
    // let categories: string[] = [];
    if (this.props.currentPosition !== null) {
      for (let item of this.props.currentPosition.requirements) {
        let check = newData.find(
          (itm) =>
            itm.category.user_detail_category_id ===
            item.category.user_detail_category_id
        );
        if (check === undefined) {
          newData = [...newData, item];
        } else {
          newData = newData.filter(
            (itm) =>
              itm.category.user_detail_category_id !==
              check?.category.user_detail_category_id
          );
          newData = [
            ...newData,
            {
              district_id: check.district_id,
              district_name: check.district_name,
              category: {
                description: check.category.description,
                fields: check.category.fields,
                title: check.category.title,
                user_detail_category_id: check.category.user_detail_category_id,
              },
              subcategory: check.subcategory,
              position_requirement: [
                ...check.position_requirement,
                ...item.position_requirement,
              ],
              required: check.required,
            },
          ];
        }
      }
    } else {
      return null;
    }
    return newData;
  };

  AddFile = (item: PositionRequirements, itm: PositionSubcategoryData) => {
    (this.validateRequirementItemOptional(item, itm) === false ||
      this.validateRequirementItem(item, itm) === false) &&
      this.props.currentPosition !== null &&
      this.setState({
        selectedRequirement: {
          selected_category: {
            description: item.category.description,
            fields: item.category.fields,
            title: item.category.title,
            user_detail_category_id: item.category.user_detail_category_id,
          },
          selected_sub_category:
            itm.subcategory_id === null
              ? {
                  category_id: item.subcategory.category_id,
                  description: item.subcategory.description,
                  title: item.subcategory.title,
                  user_detail_subcategory_id:
                    item.subcategory.user_detail_subcategory_id,
                }
              : {
                  category_id: this.props.currentPosition!.requirements.find(
                    (it) =>
                      it.subcategory.user_detail_subcategory_id ===
                      itm.subcategory_id
                  )!.subcategory.category_id,
                  description: this.props.currentPosition!.requirements.find(
                    (it) =>
                      it.subcategory.user_detail_subcategory_id ===
                      itm.subcategory_id
                  )!.subcategory.description,
                  title: this.props.currentPosition!.requirements.find(
                    (it) =>
                      it.subcategory.user_detail_subcategory_id ===
                      itm.subcategory_id
                  )!.subcategory.title,
                  user_detail_subcategory_id:
                    this.props.currentPosition!.requirements.find(
                      (it) =>
                        it.subcategory.user_detail_subcategory_id ===
                        itm.subcategory_id
                    )!.subcategory.user_detail_subcategory_id,
                },
          selected_category_subcategory_data: {
            category_id: item.category.user_detail_category_id,
            description: itm.description,
            others: [],
            subcategory_id: item.subcategory.user_detail_subcategory_id,
            title: itm.title,
            _id: itm._id,
          },
          status: true,
        },
      });
  };

  componentDidMount = () => {
    if (
      this.props.auth.documents.length === 0 &&
      this.props.auth.user.user_id &&
      this.state.loadUserDetails === true
    ) {
      this.props.FC_GetProfileDetails(
        this.props.auth.user.user_id,
        (status: boolean, msg: string) => {
          if (status === true) {
            this.setState({ loadUserDetails: false });
          }
        }
      );
      this.props.initializeApplicationProcess(
        this.props.currentPosition,
        this.props.auth.user.user_id ? this.props.auth.user.user_id : null
      );
    } else {
      this.setState({ loadUserDetails: false });
    }
    if (
      this.props.periods.applicationPeriods === null ||
      this.props.periods.applicationPeriods.length === 0
    ) {
      this.props.loadApplicationPeriods(this.setLoading);
    }
  };

  render() {
    if (this.state.redirect === true) {
      return <Redirect to="/applications" />;
    }
    return (
      <Fragment>
        <div>
          <div className="bg-white min-h-screen">
            <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24">
              <div className="pt-5 mt-5 pb-5">
                <span className="text-xl">Required documents</span>
              </div>
              {/* Documents here */}
              {(this.props.auth.documents.length === 0 &&
                this.state.loading === true) ||
              this.state.loadUserDetails === true ? (
                <div className="mt-5 bg-white rounded p-4">
                  <span className="font-light text-gray-900 ml-4 mb-4 animate-pulse">
                    Checking position requirements, Please wait...
                  </span>
                  <div className="my-2"></div>
                  <Loading />
                </div>
              ) : (
                this.props.auth.user.id !== undefined &&
                this.FilteredList() !== null &&
                this.FilteredList()?.map((item, i) => (
                  <div
                    key={i + 1}
                    className={`flex flex-col rounded gap-2 ${
                      item !== null &&
                      this.props.auth.documents !== null &&
                      validateDocCategory(item, this.props.auth.documents) !==
                        null &&
                      item.position_requirement.length === 0
                        ? "bg-yellow-50 text-yellow-800 border border-yellow-100"
                        : "bg-white text-gray-800"
                    }`}
                  >
                    <div className="flex flex-row items-center justify-between border-b mb-2">
                      <div className="flex flex-row items-center gap-2 text-gray-800">
                        <div>{/* <IoMdBookmark className="text-4xl" /> */}</div>
                        <div className="flex flex-col px-2 w-full">
                          <div className="text-xl font-bold my-2 flex flex-col">
                            <span>{item.category.title} </span>

                            {item !== null &&
                            this.props.auth.user.user_id &&
                            this.props.auth.documents !== null &&
                            validateDocCategory(
                              item,
                              this.props.auth.documents
                            ) !== null &&
                            validateDocCategory(
                              item,
                              this.props.auth.documents
                            ) !== undefined &&
                            item.position_requirement.length === 0 ? (
                              <span className="text-base text-yellow-800 font-bold">
                                <span className={`underline`}>
                                  {item.subcategory.title}
                                </span>
                              </span>
                            ) : (
                              item.position_requirement.length === 0 && (
                                <span className="text-base text-blue-600 font-bold">
                                  <span className={`underline`}>
                                    {item.subcategory.title}
                                  </span>
                                </span>
                              )
                            )}
                          </div>
                          {/* {console.log("User miiss: ", item.position_requirement)} */}
                        </div>
                      </div>
                      <div className="flex items-center justify-end">
                        {item !== null &&
                        this.props.auth.user.user_id &&
                        this.props.auth.documents !== null &&
                        validateDocCategory(item, this.props.auth.documents) !==
                          null &&
                        validateDocCategory(item, this.props.auth.documents) !==
                          undefined &&
                        item.position_requirement.length === 0 ? (
                          <div
                            onClick={() =>
                              this.setState({
                                selectedRequirement: {
                                  selected_category: {
                                    description: item.category.description,
                                    fields: item.category.fields,
                                    title: item.category.title,
                                    user_detail_category_id:
                                      item.category.user_detail_category_id,
                                  },
                                  selected_sub_category: item.subcategory,
                                  status: true,
                                },
                              })
                            }
                            title="Click to add a qualification"
                            className="btn border border-yellow-500  flex-row items-center text-yellow-800 bg-white w-max cursor-pointer hover:bg-yellow-700 hover:text-white hover:border-yellow-800"
                          >
                            Add document
                          </div>
                        ) : (
                          <div></div>
                        )}
                      </div>
                    </div>
                    {(this.state.loadUserDetails === false ||
                      this.state.loading === false) &&
                      item.position_requirement.length > 0 && (
                        <div className="w-full overflow-x-auto">
                          <div className="grid md:grid-cols-3 gap-5">
                            {item.position_requirement.map((itm, j) =>
                              this.validateRequirementItem(item, itm) ===
                              false ? (
                                <EmptyFile
                                  item={item}
                                  data={itm}
                                  currentPosition={this.props.currentPosition}
                                  auth={this.props.auth}
                                  addFile={() => this.AddFile(item, itm)}
                                  validateRequirementItem={
                                    this.validateRequirementItem
                                  }
                                />
                              ) : (
                                <SuccessFile
                                  item={item}
                                  data={itm}
                                  currentPosition={this.props.currentPosition}
                                  auth={this.props.auth}
                                  validateRequirementItem={
                                    this.validateRequirementItem
                                  }
                                  validateRequirementItemOptional={
                                    this.validateRequirementItemOptional
                                  }
                                  addFile={() => this.AddFile(item, itm)}
                                />
                              )
                            )}
                          </div>
                        </div>
                      )}
                  </div>
                ))
              )}
            </div>
            {/* Footer */}
            <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24">
              <div className="flex flex-row justify-between items-center border-t border-gray-300 my-5 pt-3 pb-5">
                <button
                  onClick={() =>
                    this.state.loadUserDetails === false &&
                    this.props.initializeApplicationProcess(null, null)
                  }
                  className="btn bg-white text-gray-600 border border-gray-300"
                >
                  Back
                </button>
                {this.props.auth.user.user_id === "" && (
                  <div className="btn bg-yellow-100 text-yellow-800 hover:text-yellow-900">
                    Login to proceed
                  </div>
                )}
                {this.props.auth.user.user_id &&
                  this.props.candidateApply.selectedPosition !== null &&
                  this.props.candidateApply.user_id !== null &&
                  this.props.currentPosition !== null &&
                  this.state.loadUserDetails === false &&
                  this.state.loading === false &&
                  returnValidatePosition(
                    this.props.currentPosition.requirements,
                    this.props.auth.documents
                  ) === true && (
                    <button
                      onClick={() =>
                        this.props.setCandidateApplicationStep(
                          ApplicationStepsTypes.Step2
                        )
                      }
                      className="btn bg-green-500 text-white"
                    >
                      Continue
                    </button>
                  )}
                {this.state.loadUserDetails === true ||
                this.state.loading === true ? (
                  <button className="btn bg-gray-100 text-gray-600 cursor-not-allowed">
                    Loading, Please wait...
                  </button>
                ) : (
                  this.props.auth.user.user_id &&
                  this.props.candidateApply.selectedPosition !== null &&
                  this.props.candidateApply.user_id !== null &&
                  this.props.currentPosition !== null &&
                  returnValidatePosition(
                    this.props.currentPosition.requirements,
                    this.props.auth.documents
                  ) === false && (
                    <button
                      onClick={() =>
                        this.setState({ submitWithoutRequirements: true })
                      }
                      className="btn bg-blue-100 text-blue-600"
                    >
                      Continue anyway
                    </button>
                  )
                )}
              </div>
            </div>
          </div>
        </div>

        {/* Add missing document modal */}
        {this.state.selectedRequirement !== null &&
        this.props.auth.user.user_id ? (
          <AddUserDocument
            active={this.state.selectedRequirement.status}
            close={() => this.setState({ selectedRequirement: null })}
            category={this.state.selectedRequirement.selected_category}
            sub_category={this.state.selectedRequirement.selected_sub_category}
            category_subcategory_data={
              this.state.selectedRequirement.selected_category_subcategory_data
            }
            UploadCompleted={() => {
              this.setState({ selectedRequirement: null });
              this.props.PushPopupAlert({
                title: `Data added successfully`,
                theme: PopupAlertTheme.success,
              });
            }}
          />
        ) : (
          this.state.selectedRequirement !== null && (
            <Modal
              title=""
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ selectedRequirement: null })}
              backDropClose={true}
              widthSizeClass={ModalSize.medium}
              displayClose={false}
              padding={{ title: false, body: true }}
            >
              {this.state.loading === true ? (
                <Loading />
              ) : (
                <div className="">
                  <div className="flex flex-row items-center gap-3">
                    <RiErrorWarningLine className="text-6xl text-yellow-700" />
                    <div className="flex flex-col gap-2">
                      <span className="text-xl font-extrabold text-gray-700">
                        Please login to proceed
                      </span>
                      <span className="text-xs text-gray-800">
                        You need to be authenticated so that you can proceed.
                        Please login or create an account!
                      </span>
                    </div>
                  </div>
                  <div className="flex flex-row justify-between items-center mt-4">
                    <div
                      onClick={() =>
                        this.setState({ selectedRequirement: null })
                      }
                      className="btn bg-gray-100 text-gray-800 cursor-pointer"
                    >
                      Cancel
                    </div>
                    <div className="btn bg-blue-100 text-blue-600 hover:bg-blue-800 hover:text-white cursor-pointer">
                      Yes, Login
                    </div>
                  </div>
                </div>
              )}
            </Modal>
          )
        )}
        {/* {requirementsModal === true &&
        <Modal
          title="Add requirement"
          backDrop={true}
          theme={Themes.primary}
          close={() => setRequirementsModal(false)}
          backDropClose={true}
          widthSizeClass={ModalSize.large}
          displayClose={true}
          padding={{title: true, body: true}}
        >
          <div>
            content
          </div>
        </Modal> */}
        {/* } */}
        {this.state.submitWithoutRequirements === true && (
          <Modal
            title=""
            backDrop={true}
            theme={Themes.warning}
            close={() => this.setState({ submitWithoutRequirements: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.medium}
            displayClose={false}
            padding={{ body: true }}
          >
            {this.state.loading === true ? (
              <Loading />
            ) : (
              <div className="">
                <div className="flex flex-row items-center gap-3">
                  <RiErrorWarningLine className="text-9xl text-yellow-700" />
                  <div className="flex flex-col gap-2">
                    <span className="text-xl font-extrabold text-gray-700">
                      Submit application without requirements?
                    </span>
                    <span className="text-xs text-gray-800">
                      To submit the application without filling all
                      requirements, make sure that you know your decision
                      because you have low chance to get shortlisted!
                    </span>
                  </div>
                </div>
                <div className="flex flex-row justify-between items-center">
                  <div
                    onClick={() =>
                      this.state.loadUserDetails === false &&
                      this.setState({ submitWithoutRequirements: false })
                    }
                    className="btn bg-gray-100 text-gray-800 cursor-pointer"
                  >
                    Back
                  </div>
                  <div
                    onClick={() => this.sendApplication()}
                    className="btn bg-yellow-700 text-white hover:bg-yellow-900 cursor-pointer"
                  >
                    Yes, I confirm
                  </div>
                </div>
              </div>
            )}
          </Modal>
        )}
      </Fragment>
    );
  }
}

export default CandidateApplyRequirements;
