import React, { Fragment } from "react";
import { RiSearchLine } from "react-icons/ri";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { education_category, school_level } from "../../../interfaces";

interface HeaderProps {
  loading: boolean;
  education_categories: education_category[];
  selectedEducationCategory: education_category | null;
  school_levels: school_level[];
  setSelectedEducationCategory: (
    education_category: education_category
  ) => void;
  searchData: string;
  setSearchData: (value: string) => void;
}

const Header = (props: HeaderProps) => {
  const selectedClass =
    "bg-white text-blue-500 font-bold px-5 py-1 rounded-t-lg text-lg";
  const unSelectedClass =
    "bg-transparent text-white px-5 py-1 rounded-t-lg text-lg hover:bg-blue-400 cursor-pointer";
  return (
    <Fragment>
      <div>
        <div className="bg-blue-500 p-3 pb-0 md:p-5 md:pb-0">
          <div className="flex flex-col items-center justify-center gap-1 ">
            <div className="text-center mb-5">
              <span className="text-white text-center font-extrabold text-2xl md:text-2xl">
                Welcome to Teacher Recruitment Platform
              </span>
            </div>
            <div className="w-full hidden items-center justify-center mt-2 md:mt-2">
              {/* Search */}
              <div className="w-full md:w-3/5">
                <input
                  value={props.searchData}
                  onChange={(e) => {
                    props.setSearchData(e.target.value);
                  }}
                  type="search"
                  className="bg-white rounded-md px-4 pl-12 h-10 w-full shadow-lg"
                  placeholder="Search a position..."
                />
                <div className="relative -mt-8 mb-4 ml-3 w-max">
                  <RiSearchLine className="text-2xl text-blue-400" />
                </div>
              </div>
            </div>
            <div className="">
              {/* Institutes */}
              <div className="flex flex-row items-center justify-center">
                {props.education_categories.map((item, i) => (
                  <div
                    key={i + 1}
                    title={item.description}
                    className={`${
                      item.education_institute_id ===
                      props.selectedEducationCategory?.education_institute_id
                        ? selectedClass
                        : unSelectedClass
                    }`}
                    onClick={() => {
                      props.setSelectedEducationCategory(item);
                    }}
                  >
                    <span className="uppercase">
                      {item.education_category_title}
                    </span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
      {props.loading === false && props.selectedEducationCategory === null && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={() => console.log("Closed")}
          backDropClose={false}
          widthSizeClass={ModalSize.medium}
          displayClose={false}
          padding={{
            title: undefined,
            body: undefined,
            footer: undefined,
          }}
        >
          <div>
            <div className="p-3">
              <div className="text-center text-2xl text-gray-600 font-bold mb-5 mt-3 ">
                Choose your interest
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
                {props.education_categories.map((item, i) => (
                  <div
                    key={i + 1}
                    onClick={() => {
                      props.setSelectedEducationCategory(item);
                    }}
                    className="col-span-1 bg-blue-100 rounded p-5 cursor-pointer hover:bg-blue-500 group animate__animated animate__fadeIn animate__faster"
                  >
                    <div className="text-3xl font-bold text-blue-800 group-hover:text-white uppercase">
                      {item.education_category_title}
                    </div>
                    <div className="text-blue-700 group-hover:text-white font-light">
                      {item.description}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </Modal>
      )}
    </Fragment>
  );
};

export default React.memo(Header);
