import React, { useState } from "react";
import { CgArrowsExchange } from "react-icons/cg";
import { IoIosArrowDown } from "react-icons/io";
import { MdOutlineSettingsBackupRestore } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import { VscThreeBars } from "react-icons/vsc";
import { education_category, school_level } from "../../../interfaces";

interface SchoolLevelsFilterProps {
  school_levels: school_level[];
  selectedSchoolLevel: school_level | null;
  setSelectedSchoolLevel: (education_category: school_level) => void;
  selectedEducationCategory: education_category | null;
  searchData: string;
  setSearchData: (value: string) => void;
  cleanSelectedSchoolLevel: () => void;
}

const SchoolLevelsFilter = (props: SchoolLevelsFilterProps) => {
  const [showLevelsList, setShowLevelsList] = useState<boolean>(false);
  return (
    <div>
      <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 bg-white border-b border-gray-300 pb-3 md:pb-5 z-50 animate__animated animate__fadeIn animate__faster">
        <div className="flex flex-col md:flex-row pt-5 md:pt-0 items-center gap-3 justify-center w-full">
          <div className="w-full">
            <div
              title="Filter by school levels"
              onClick={() => setShowLevelsList(!showLevelsList)}
              className={`px-3 pl-2 py-2 rounded border ${
                showLevelsList !== true ? "border-gray-400" : "border-blue-500"
              } hover:border-blue-500 flex flex-row items-center gap-3 justify-between cursor-pointer group`}
            >
              <div className="flex flex-row items-center gap-1">
                {props.selectedSchoolLevel !== null && (
                  <div className="flex flex-row items-center">
                    <div className="bg-blue-10 rounded-lg px-2 underline text-blue-500">
                      {props.selectedSchoolLevel.school_level_name}
                    </div>
                    <div>
                      <CgArrowsExchange className="text-2xl text-gray-500 -my-2" />
                    </div>
                  </div>
                )}
                <div
                  className={`${
                    showLevelsList === true ? "text-blue-500" : ""
                  } group-hover:text-blue-500`}
                >
                  {props.selectedSchoolLevel === null ? (
                    <span>Choose School level</span>
                  ) : (
                    <span className="text-gray-400">Change School level</span>
                  )}
                </div>
              </div>
              <div>
                <IoIosArrowDown className="text-2xl -m-2 text-gray-400 group-hover:text-blue-500" />
              </div>
            </div>
            {/* School levels */}
            {showLevelsList === true && (
              <div className="bg-white z-50 rounded-b p-2 absolute w-full md:w-1/2 border border-t-0 animate__animated animate__fadeInBottom animate__faster">
                <div
                  onClick={() => {
                    props.cleanSelectedSchoolLevel();
                    setShowLevelsList(!showLevelsList);
                  }}
                  className={`flex flex-row items-center gap-3 rounded mb-1 cursor-pointer ${
                    props.selectedSchoolLevel === null
                      ? "bg-blue-500 text-white"
                      : "bg-white hover:bg-blue-100"
                  } group`}
                >
                  <div
                    className={`p-2 font-semibold ${
                      props.selectedSchoolLevel === null
                        ? "text-white"
                        : "text-gray-600 group-hover:text-blue-600"
                    }`}
                  >
                    <div className="flex flex-row items-center gap-3">
                      <div>
                        {props.selectedSchoolLevel === null ? (
                          <VscThreeBars className="text-2xl" />
                        ) : (
                          <MdOutlineSettingsBackupRestore className="text-2xl" />
                        )}
                      </div>
                      <span>All levels</span>
                    </div>
                  </div>
                </div>
                {props.school_levels
                  .filter(
                    (itm) =>
                      itm.school_level_id ===
                      props.selectedEducationCategory?.school_levels.find(
                        (test) => test === itm.school_level_id
                      )
                  )
                  .map((item, i) => (
                    <div
                      key={i + 1}
                      onClick={() => {
                        props.setSelectedSchoolLevel(item);
                        setShowLevelsList(!showLevelsList);
                      }}
                      className={`flex flex-row items-center gap-3 rounded mb-1 cursor-pointer ${
                        item.school_level_id ===
                        props.selectedSchoolLevel?.school_level_id
                          ? "bg-blue-500 text-white"
                          : "bg-white hover:bg-blue-100"
                      } group`}
                    >
                      <div
                        className={`p-2 font-semibold ${
                          item.school_level_id ===
                          props.selectedSchoolLevel?.school_level_id
                            ? "text-white"
                            : "text-gray-600 group-hover:text-blue-600"
                        }`}
                      >
                        {item.school_level_name}
                      </div>
                    </div>
                  ))}
              </div>
            )}
          </div>
          {/* Search input */}
          <div className="w-full mt-2">
            <input
              value={props.searchData}
              onChange={(e) => {
                props.setSearchData(e.target.value);
              }}
              type="search"
              className="bg-white rounded px-4 pl-12 h-10 w-full border border-gray-400"
              placeholder="Search a position..."
            />
            <div className="relative -mt-8 mb-4 ml-3 w-max">
              <RiSearchLine className="text-2xl text-gray-400" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default React.memo(SchoolLevelsFilter);
