import React, { Component, Fragment } from "react";
import { IoMdInformationCircleOutline } from "react-icons/io";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {
  Auth,
  positionsStoreInterface,
  applicationPeriodsStoreInterface,
  applicationStoreInterface,
  System,
  schoolLevelsStoreInterface,
  loadEducationCategories,
  loadSchoolLevels,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  loadApplicationPeriods,
  loadAllPositions,
  setSelectedPeriod,
  cleanSelectedSchoolLevel,
  setSelectedPosition,
  candidateApplyStoreInterface,
  initializeApplicationProcess,
  FT_LoginModal,
  FC_LoginModal,
  cleanCandidateApply,
} from "../../actions";
import {
  FindActiveInPlans,
  FindPlan,
} from "../../components/ApplicationPeriods/ApplicationPeriods";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import {
  education_category,
  PositionAPI,
  school_level,
} from "../../interfaces";
import {
  ApplicationPeriodGet,
  SESSION_PLAN_KEYS,
} from "../../interfaces/application";
import { StoreState } from "../../reducers";
import { DATE } from "../../utils/functions";
import search from "../../utils/search";
import Header from "./ApplicationComponents/Header";
import HeaderLazyLoading from "./ApplicationComponents/HeaderLazyLoading";
import PositionDetails from "./ApplicationComponents/PositionDetails";
import PositionItem from "./ApplicationComponents/PositionItem";
import PositionsLazyLoading from "./ApplicationComponents/PositionsLazyLoading";
import SchoolLevelsFilter from "./ApplicationComponents/SchoolLevelsFilter";
import { CandidateApply } from "./Apply/CandidateApply";
interface CandidateApplicationProps {
  auth: Auth;
  application: applicationStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  system: System;
  candidateApply: candidateApplyStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  setSelectedEducationCategory: (
    education_category: education_category
  ) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  setSelectedSchoolLevel: (education_category: school_level) => void;
  loadAllPositions: (callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  cleanSelectedSchoolLevel: () => void;
  setSelectedPosition: (position: PositionAPI) => void;
  initializeApplicationProcess: (
    position: PositionAPI | null,
    user_id: string | null
  ) => void;
  FC_LoginModal: FT_LoginModal;
  cleanCandidateApply: () => void;
}
interface CandidateApplicationState {
  loading: boolean;
  searchData: string;
  ViewPositionDetails: PositionAPI | null;
  startApply: boolean;
}

class _CandidateApplication extends Component<
  CandidateApplicationProps,
  CandidateApplicationState
> {
  constructor(props: CandidateApplicationProps) {
    super(props);

    this.state = {
      loading: false,
      searchData: "",
      ViewPositionDetails: null,
      startApply: false,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  setSearchData = (value: string) => {
    this.setState({ searchData: value });
  };

  FilterPositions = (positions: PositionAPI[]) => {
    let response: PositionAPI[] = positions;
    response =
      this.props.schoolLevels.selectedEducationCategory !== null
        ? response.filter(
            (itm) =>
              itm.education_institute_id.education_institute_id ===
              this.props.schoolLevels.selectedEducationCategory
                ?.education_institute_id
          )
        : response;
    response =
      this.props.schoolLevels.selectedSchoolLevel !== null
        ? response.filter(
            (itm) =>
              itm.school_level_id.school_level_id ===
              this.props.schoolLevels.selectedSchoolLevel?.school_level_id
          )
        : response;
    response = search(response, this.state.searchData) as PositionAPI[];
    return response;
  };
  componentDidMount = () => {
    if (this.props.schoolLevels.education_categories === null) {
      this.props.loadEducationCategories(this.setLoading);
    }
    if (this.props.schoolLevels.school_levels === null) {
      this.props.loadSchoolLevels(this.setLoading);
    }
    if (this.props.periods.applicationPeriods === null) {
      this.props.loadApplicationPeriods(this.setLoading);
    }
  };
  render() {
    let ActiveSession = null;
    if (this.props.periods.applicationPeriods !== null) {
      let myFind = this.props.periods.applicationPeriods.find(
        (itm) => itm.is_active === true
      );
      if (myFind !== undefined) {
        ActiveSession = myFind;
      }
    }
    if (this.props.candidateApply.selectedPosition !== null)
      return (
        <div
          className={`${
            this.props.auth.isAuthenticated === true ? "ml-0 md:-ml-3" : ""
          }`}
        >
          <CandidateApply
            initializeApplicationProcess={(
              position: PositionAPI | null,
              user_id: string | null
            ) => {
              this.props.initializeApplicationProcess(position, user_id);
            }}
            cleanCandidateApply={() => {
              this.props.cleanCandidateApply();
              this.setState({ ViewPositionDetails: null });
            }}
          />
          ;
        </div>
      );
    return (
      <Fragment>
        <div className="min-h-screen">
          <div
            className={`min-h-full ${
              this.props.auth.isAuthenticated === true ? "ml-0 md:-ml-3" : ""
            }`}
          >
            {/* Header section */}
            <div>
              {this.state.loading === true ||
              this.props.schoolLevels.education_categories === null ||
              this.props.schoolLevels.school_levels === null ? (
                <HeaderLazyLoading />
              ) : (
                <Header
                  education_categories={
                    this.props.schoolLevels.education_categories
                  }
                  school_levels={this.props.schoolLevels.school_levels}
                  selectedEducationCategory={
                    this.props.schoolLevels.selectedEducationCategory
                  }
                  setSelectedEducationCategory={(
                    education_category: education_category
                  ) => {
                    this.props.setSelectedEducationCategory(education_category);
                    if (this.props.positions.positions_list === null) {
                      this.props.loadAllPositions(this.setLoading);
                    }
                  }}
                  searchData={this.state.searchData}
                  setSearchData={this.setSearchData}
                  loading={this.state.loading}
                />
              )}
            </div>
            {/* Current session */}
            {this.state.loading === false &&
              this.props.schoolLevels.selectedEducationCategory !== null && (
                <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 bg-white flex flex-col md:flex-row md:items-center gap-1 md:gap-3 md:justify-between pt-3 pb-1">
                  <div>
                    <span className="text-3xl">Published positions</span>
                  </div>
                  <div>
                    {ActiveSession !== null &&
                    FindActiveInPlans(ActiveSession.plans).find(
                      (itm) => itm.type_key === SESSION_PLAN_KEYS.APPLICATION
                    ) === undefined ? (
                      <div className="bg-white rounded-md px-6 py-6 my-6 text-center">
                        <div className="flex flex-col items-center">
                          <IoMdInformationCircleOutline className="text-5xl md:text-8xl text-yellow-700" />
                        </div>
                        <span className="text-yellow-700 font-extrabold text-4xl">
                          Application is closed
                        </span>
                        {ActiveSession.plans.find(
                          (itm) =>
                            itm.type_key === SESSION_PLAN_KEYS.APPLICATION
                        ) !== undefined ? (
                          <div>
                            <span className="text-gray-500">
                              Make sure you access the system between the
                              following dates
                            </span>
                            <div className="flex flex-row items-center justify-center gap-4 mt-4">
                              <span className="font-bold text-gray-900 bg-blue-50 rounded-full px-3 py-1">
                                Start date:{" "}
                                {DATE(
                                  ActiveSession.plans.find(
                                    (itm) =>
                                      itm.type_key ===
                                      SESSION_PLAN_KEYS.APPLICATION
                                  )!.start_date,
                                  "DD/MM/YYYY"
                                )}
                              </span>
                              <span className="font-bold text-gray-900 bg-blue-50 rounded-full px-3 py-1">
                                Ending date:{" "}
                                {DATE(
                                  ActiveSession.plans.find(
                                    (itm) =>
                                      itm.type_key ===
                                      SESSION_PLAN_KEYS.APPLICATION
                                  )!.end_date,
                                  "DD/MM/YYYY"
                                )}
                              </span>
                            </div>
                          </div>
                        ) : (
                          <div className="text-sm text-gray-500">
                            For this session, no application phase has been
                            activated yet,
                            <br />
                            please keep in touch with the system to know the
                            time for it or
                            <br />
                            contact the support center near your location
                          </div>
                        )}
                        <div className="mt-6">
                          <Link
                            className="text-blue-700 font-bold underline mt-6"
                            to="/applications"
                          >
                            Go back to my applications
                          </Link>
                        </div>
                      </div>
                    ) : (
                      <div className="col-span-1 flex flex-col md:items-end -mb-2 md:-mt-2 bg-white p-2 md:p-0 md:bg-transparent rounded md:rounded-none">
                        <div className="flex flex-row gap-2 md:text-right">
                          <span className="text-gray-800">Session: </span>
                          <span className="font-extrabold text-blue-600">
                            {
                              this.props.periods.selected_period
                                ?.session_plan_name
                            }
                          </span>
                        </div>
                        {this.props.periods.selected_period &&
                        FindPlan(
                          this.props.periods.selected_period.plans,
                          SESSION_PLAN_KEYS.APPLICATION
                        ) !== null ? (
                          <>
                            <div className="flex flex-row gap-2 text-sm text-right">
                              <span className="text-gray-800">Start date:</span>
                              <span className="font-extrabold">
                                {this.props.periods.selected_period &&
                                  DATE(
                                    FindPlan(
                                      this.props.periods.selected_period.plans,
                                      SESSION_PLAN_KEYS.APPLICATION
                                    )!.start_date,
                                    "DD/MM/YYYY"
                                  )}
                              </span>
                            </div>
                            <div className="flex flex-row gap-2 text-sm text-right items-center">
                              <span className="text-gray-800">
                                Ending date:
                              </span>
                              <span className="font-extrabold text-yellow-900 bg-yellow-100 border-2 border-white rounded px-2 py-1">
                                {this.props.periods.selected_period &&
                                  DATE(
                                    FindPlan(
                                      this.props.periods.selected_period.plans,
                                      SESSION_PLAN_KEYS.APPLICATION
                                    )!.end_date,
                                    "DD/MM/YYYY"
                                  )}
                              </span>
                            </div>
                          </>
                        ) : (
                          <div>Application is not open</div>
                        )}
                      </div>
                    )}
                  </div>
                </div>
              )}
            {/* Filter */}
            <div className="mt-3 md:mt-0">
              {this.props.schoolLevels.school_levels !== null &&
                this.props.schoolLevels.selectedEducationCategory !== null &&
                this.props.positions.positions_list !== null && (
                  <SchoolLevelsFilter
                    cleanSelectedSchoolLevel={
                      this.props.cleanSelectedSchoolLevel
                    }
                    school_levels={this.props.schoolLevels.school_levels}
                    selectedSchoolLevel={
                      this.props.schoolLevels.selectedSchoolLevel
                    }
                    setSelectedSchoolLevel={this.props.setSelectedSchoolLevel}
                    selectedEducationCategory={
                      this.props.schoolLevels.selectedEducationCategory
                    }
                    searchData={this.state.searchData}
                    setSearchData={this.setSearchData}
                  />
                )}
            </div>
            {/* POSITIONS LIST */}
            <div className="pt-2 pb-10 min-h-full">
              <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 min-h-full">
                {this.props.positions.positions_list === null ? (
                  <PositionsLazyLoading />
                ) : this.FilterPositions(this.props.positions.positions_list)
                    .length === 0 ? (
                  <div>
                    <div className="bg-gray-100 rounded-md p-10 text-center text-gray-500 text-3xl">
                      No data found!
                    </div>
                  </div>
                ) : (
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
                    {this.FilterPositions(
                      this.props.positions.positions_list
                    ).map((item, i) => (
                      <PositionItem
                        item={item}
                        key={i + 1}
                        onClick={() => {
                          if (this.props.auth.login_modal === true) {
                            this.props.FC_LoginModal(false);
                          }
                          this.setState({ ViewPositionDetails: item });
                        }}
                      />
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        {this.state.ViewPositionDetails !== null &&
          (this.props.auth.login_modal === false ||
            this.props.auth.isAuthenticated === true) && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ ViewPositionDetails: null })}
              backDropClose={true}
              widthSizeClass={ModalSize.large}
              displayClose={true}
              title={
                <div>
                  <div className="flex flex-row items-center gap-3">
                    <span className="font-bold text-xl">
                      {this.state.ViewPositionDetails.position_code} -{" "}
                      {this.state.ViewPositionDetails.position_title}
                    </span>
                    <span></span>
                  </div>
                  <div className="flex flex-row items-center gap-3 text-sm font-light">
                    <span className="text-gray-500">School level</span>
                    <span className="">
                      {
                        this.state.ViewPositionDetails.school_level_id
                          .school_level_name
                      }
                    </span>
                  </div>
                  <div className="flex flex-row items-center gap-3 text-sm font-light">
                    <span className="text-gray-500">School level</span>
                    <span className="uppercase font-bold">
                      {
                        this.state.ViewPositionDetails.education_institute_id
                          .education_category_title
                      }
                    </span>
                  </div>
                </div>
              }
              padding={{
                title: true,
                body: undefined,
                footer: undefined,
              }}
            >
              <div className="border-t border-gray-400">
                <PositionDetails
                  cancel={() => {
                    this.setState({ ViewPositionDetails: null });
                  }}
                  apply={() => {
                    // Check auth
                    if (this.props.auth.isAuthenticated === false) {
                      this.props.FC_LoginModal(true);
                      this.setState({
                        ViewPositionDetails: this.state.ViewPositionDetails,
                      });
                    } else {
                      this.props.FC_LoginModal(false);
                      this.state.ViewPositionDetails &&
                        this.props.initializeApplicationProcess(
                          this.state.ViewPositionDetails,
                          this.props.auth.user.user_id
                        );
                    }
                  }}
                  positionDetails={this.state.ViewPositionDetails}
                />
              </div>
            </Modal>
          )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  system,
  positions,
  periods,
  application,
  schoolLevels,
  candidateApply,
}: StoreState): {
  auth: Auth;
  application: applicationStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  system: System;
  candidateApply: candidateApplyStoreInterface;
} => {
  return {
    auth,
    system,
    application,
    positions,
    periods,
    schoolLevels,
    candidateApply,
  };
};

export const CandidateApplication = connect(mapStateToProps, {
  loadEducationCategories,
  loadSchoolLevels,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  loadApplicationPeriods,
  loadAllPositions,
  setSelectedPeriod,
  cleanSelectedSchoolLevel,
  setSelectedPosition,
  initializeApplicationProcess,
  FC_LoginModal,
  cleanCandidateApply,
})(_CandidateApplication);
