import React from "react";
import { PositionAPI, PositionRequirements } from "../../../interfaces";
import { BsArrowRight } from "react-icons/bs";
import { FC_IsJson } from "../../../utils/functions";

interface PositionDetailsProps {
  positionDetails: PositionAPI;
  cancel: () => void;
  apply: () => void;
}

const PositionDetails = (props: PositionDetailsProps) => {
  const FilteredList = (): PositionRequirements[] | null => {
    let newData: PositionRequirements[] = [];
    // let categories: string[] = [];
    if (props.positionDetails !== null) {
      for (let item of props.positionDetails.requirements) {
        let check = newData.find(
          (itm) =>
            itm.category.user_detail_category_id ===
            item.category.user_detail_category_id
        );
        if (check === undefined) {
          newData = [...newData, item];
        } else {
          newData = newData.filter(
            (itm) =>
              itm.category.user_detail_category_id !==
              check?.category.user_detail_category_id
          );
          newData = [
            ...newData,
            {
              district_id: check.district_id,
              district_name: check.district_name,
              category: {
                description: check.category.description,
                fields: check.category.fields,
                title: check.category.title,
                user_detail_category_id: check.category.user_detail_category_id,
              },
              subcategory: check.subcategory,
              position_requirement: [
                ...check.position_requirement,
                ...item.position_requirement,
              ],
              required: check.required,
            },
          ];
        }
      }
    } else {
      return null;
    }
    return newData;
  };
  return (
    <div className="p-0">
      {/* Position competencies */}
      <div className="p-4">
        <div>
          <span className="text-base font-semibold">Position competencies</span>
        </div>
        <div>
          <table className="w-full text-left mt-2">
            <thead>
              <tr className="bg-blue-100 text-blue-900">
                <th className="text-center py-1 px-0">#</th>
                <th className="py-1 px-3">Competency</th>
                <th className="py-1 px-3">Status</th>
              </tr>
            </thead>
            <tbody>
              {FC_IsJson(props.positionDetails.description) === false ? (
                <tr className="font-light">
                  <td className="text-center py-1 px-3">1</td>
                  <td className="py-1 px-3 w-full">
                    {props.positionDetails.description}
                  </td>
                  <td className="py-1 px-3">required</td>
                </tr>
              ) : JSON.parse(props.positionDetails.description).length === 0 ? (
                <tr>
                  <td colSpan={3} className="text-center text-gray-400 text-lg">
                    No exams found for this position!
                  </td>
                </tr>
              ) : (
                (JSON.parse(props.positionDetails.description) as string[]).map(
                  (item, i) => (
                    <tr
                      key={i + 1}
                      className={`${
                        i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                      } font-light`}
                    >
                      <td className="text-center py-1 px-3">{i + 1}</td>
                      <td className="py-1 px-3 w-full">{item}</td>
                      <td className="py-1 px-3">required</td>
                    </tr>
                  )
                )
              )}
            </tbody>
          </table>
        </div>
      </div>
      {/* Exams to be done on the position */}
      <div className="p-4">
        <div>
          <span className="text-base font-semibold">
            Exams for this position
          </span>
        </div>
        <div>
          <table className="w-full text-left mt-2">
            <thead>
              <tr className="bg-blue-100 text-blue-900">
                <th className="text-center py-1 px-0">#</th>
                <th className="py-1 px-3">Exam name</th>
                <th className="py-1 px-3">Code</th>
              </tr>
            </thead>
            <tbody>
              {props.positionDetails.courses.length === 0 ? (
                <tr>
                  <td colSpan={3} className="text-center text-gray-400 text-lg">
                    No exams found for this position!
                  </td>
                </tr>
              ) : (
                props.positionDetails.courses.map((item, i) => (
                  <tr
                    key={i + 1}
                    className={`${
                      i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                    } font-light`}
                  >
                    <td className="text-center py-1 px-3">{i + 1}</td>
                    <td className="py-1 px-3 w-full">{item.course_name}</td>
                    <td className="py-1 px-3">{item.course_code}</td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
      {/* Position requirements */}
      <div className="mt-5 px-4">
        <div>
          <span className="text-base font-semibold">Position requirements</span>
        </div>
        <div>
          <table className="w-full text-left mt-2">
            <thead>
              <tr className="bg-blue-100 text-blue-900">
                <th className="text-center py-1 px-3">#</th>
                <th className="py-1 px-2">Requirement</th>
                <th className="py-1 px-3"></th>
              </tr>
            </thead>
            <tbody>
              {console.log("Res: ", FilteredList())}
              {FilteredList() === null ? (
                <tr>
                  <td colSpan={3} className="text-center text-gray-400 text-lg">
                    No requirements found on this position!
                  </td>
                </tr>
              ) : FilteredList()!.length === 0 ? (
                <tr>
                  <td colSpan={3} className="text-center text-gray-400 text-lg">
                    No requirements found on this position!
                  </td>
                </tr>
              ) : (
                FilteredList()!.map((cat) =>
                  cat.position_requirement.map((item, t) => (
                    <tr
                      key={t + 1}
                      className={`${
                        t % 2 !== 0 ? "bg-gray-100" : "bg-white"
                      } font-light text-base`}
                    >
                      <td className="text-center py-1 px-3">{t + 1}</td>
                      <td className="py-1 px-2 w-full">
                        {cat.subcategory.title} in {item.title}
                      </td>
                      <td className="py-1 px-3">
                        {item.required === true ? (
                          <span className="text-yellow-900">Required</span>
                        ) : (
                          <span className="text-gray-500">Optional</span>
                        )}
                      </td>
                    </tr>
                  ))
                )
              )}
            </tbody>
          </table>
        </div>
      </div>
      {/* Buttons */}
      <div className="flex flex-row items-center justify-end mt-5 border-t border-gray-400 p-4 gap-4">
        <div
          onClick={props.cancel}
          className="cursor-pointer bg-gray-200 rounded px-3 py-2 tex-center w-max hover:bg-gray-600 hover:text-white"
        >
          <span>Cancel</span>
        </div>
        <div
          onClick={props.apply}
          className="cursor-pointer bg-blue-500 text-white rounded px-3 py-2 tex-center w-max hover:bg-blue-700 flex flex-row items-center justify-center gap-2"
        >
          <span>Apply now</span>
          <div>
            <BsArrowRight className="text-2xl" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default React.memo(PositionDetails);
