import axios from "axios";
import React, { useEffect, useState } from "react";
import {
  Auth,
  CandidateApplicationsStoreInterface,
  candidateApplyStoreInterface,
  setCandidateApplicationStep,
} from "../../../../actions";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { GetApplicationsAPI } from "../../../../interfaces/application";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { API } from "../../../../utils/api";
import { PlacementContract } from "../../../PlacementContract/PlacementContract";
import { ApplicationStepsTypes } from "../CandidateApplicationSteps/CandidateApplicationSteps";
import Congratulation from "./Congratulation";

interface AgreementProps {
  auth: Auth;
  candidateApplications: CandidateApplicationsStoreInterface;
  candidateApply: candidateApplyStoreInterface;
  SelectUserApplication: (application: GetApplicationsAPI | null) => void;
  setCandidateApplicationStep: (step: ApplicationStepsTypes) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
  RemoveUserApplication: (id: string) => void;
  setApplyCompletionStatus: (status: boolean) => void;
  cleanCandidateApply: () => void;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
}

const Agreement = (props: AgreementProps) => {
  const [changed, setChanged] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);

  const deleteApplication = async () => {
    let test = props.candidateApplications.candidate_applications?.find(
      (itm) =>
        itm.user_id === props.auth.user.user_id &&
        itm.position.position_id ===
          props.candidateApply.selectedPosition?.position_id &&
        itm.application_session.session_plan_id ===
          props.auth.application_session?.session_plan_id
    );
    if (test !== undefined) {
      setLoading(true);
      try {
        await axios.delete(
          `${API.APPLICATION}/application/${test.application_id}`
        );
        props.RemoveUserApplication(test.application_id);
        setLoading(false);
        props.PushPopupAlert({
          title: `Application process canceled successfully!`,
          description: "Now you can choose another position from the list",
          theme: PopupAlertTheme.info,
        });
      } catch (error: any) {
        setLoading(false);
        props.PushPopupAlert({
          title: `Failed to cancel application process!`,
          theme: PopupAlertTheme.warning,
        });
      }
    }
  };

  useEffect(() => {
    if (
      props.candidateApplications.candidate_applications === null ||
      (props.candidateApplications.candidate_applications !== null &&
        props.candidateApplications.candidate_applications.find(
          (itm) =>
            itm.position.position_id ===
              props.candidateApply.selectedPosition?.position_id &&
            itm.application_session.session_plan_id ===
              props.auth.application_session?.session_plan_id
        ) === undefined)
    ) {
      setLoading(true);
      // Load applications
      props.LoadCandidateApplications(
        props.auth.user.user_id,
        (status: boolean) => {
          setLoading(status);
        }
      );
    }

    if (changed === false && loading === false) {
      props.candidateApplications.candidate_applications !== null &&
        props.candidateApplications.candidate_applications !== null &&
        props.candidateApplications.candidate_applications[
          props.candidateApplications.candidate_applications.length - 1
        ] !== undefined &&
        props.candidateApplications.candidate_applications[
          props.candidateApplications.candidate_applications.length - 1
        ].user_id === props.auth.user.user_id &&
        props.SelectUserApplication(
          props.candidateApplications.candidate_applications[
            props.candidateApplications.candidate_applications.length - 1
          ]
        );
      if (props.candidateApplications.selected_application !== null) {
        setChanged(true);
      }
    }
  }, [props, changed, loading]);

  if (props.candidateApply.completed === true) {
    return (
      <Congratulation
        auth={props.auth}
        candidateApply={props.candidateApply}
        cleanCandidateApply={props.cleanCandidateApply}
      />
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 mt-5 bg-white">
      <div className="">
        <PlacementContract />
      </div>
      {/* Footer */}
      <div className="flex flex-row justify-between items-center border-t border-gray-300 mb-5 pt-3 pb-8 -mt-16">
        <button
          onClick={() => {
            if (loading === false) {
              deleteApplication(); //add loading
              props.setCandidateApplicationStep(ApplicationStepsTypes.Step2);
            }
          }}
          className="btn bg-white text-gray-600 border border-gray-300"
        >
          {loading === true ? "Canceling..." : "Back"}
        </button>
        <button
          onClick={() => {
            if (
              window.confirm(
                "Are you sure do you want to submit your application?"
              ) === true
            ) {
              props.setApplyCompletionStatus(true);
            }
          }}
          className={`btn ${
            props.candidateApply.selectedPosition?.courses.length ===
            props.candidateApply.exams?.length
              ? "bg-green-500 text-white"
              : "bg-gray-200 text-black cursor-not-allowed"
          }`}
        >
          Finish application
        </button>
      </div>
    </div>
  );
};

export default Agreement;
